
.how-it-works {
    margin-bottom: 10px;

    &__title {
        display: block;
        width: 60%;
        margin: 0 auto;
        margin-bottom: 0.8em;
        text-align: center;
    }

    &__marked {
        color: $blue;
    }

    &__img-wrapper {
        margin-bottom: 0.8em;
        border-radius: 4px;
        background-color: #ffffff;
        box-shadow: 0 12px 24px 0 rgba(0, 0, 0, 0.06);
        
        &_green {
            border: solid 1px rgba(80, 213, 179, 0.3);
        }

        &_red {
            border: solid 1px rgba(245, 60, 60, 0.3);
        }
    }
}