
.quote {
    position: relative;
    padding-top: 15px;
    padding-bottom: 19px;
    color: $dark-blue;

    &::before {
        content: '';
        position: absolute;
        top: 0; 
        left: 50%;
        transform: translate(-50%, 0);
        width: 36px;
        height: 1px;
        background: #dee1e5;
    }

    &::after {
        content: '';
        position: absolute;
        bottom: 0; 
        left: 50%;
        transform: translate(-50%, 0);
        width: 36px;
        height: 1px;
        background: #dee1e5;
    }

    &__wrapper {
        margin: 0 auto;
        width: 90%;
    }

    &__text {
        position: relative;
        display: inline-block;

        &::before {
            content: '';
            position: absolute;
            top: -7px;
            left: -15px;
            width: 20px;
            height: 20px;
            background: url('../../../img/other/quote.svg') no-repeat center;
            background-size: contain;
        }
    }

    &__author {
        font-family: "DPDorkDiary";
        text-transform: uppercase;
        font-size: 12px;
        line-height: 1.17;
        color: $dark-blue;

        &_little {
            text-transform: none;
            line-height: 1.57;
            color: #7a838e;
        }
    }
}